@font-face {
  font-family: 'Lovelo';
  src: url('LoveloBlack.otf'); 
  }

.App {
  text-align: center;
  color: #61dafb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #fffdf5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App h1 {
  font-family: 'Lovelo';
  color: #61dafb;
  font-size: 8rem;
}

.topnav {
  background-color: #89CFF0;
  overflow: hidden;
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  font-weight: 700;
  border-radius: 10px;
}

.topnav a:hover {
  background-color: #89CFF0;
  color: #f2f2f2;
}

.topnav a.active {
  background-color: #89CFF0;
  color: #f2f2f2;
}

.custom-shape-divider-bottom-1712640273 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.custom-shape-divider-bottom-1712640273 svg {
  position: relative;
  display: block;
  width: calc(152% + 1.3px);
  height: 165px;
}

.custom-shape-divider-bottom-1712640273 .shape-fill {
  fill: #89CFF0;
}

